<template>
  <div>
  
    <el-row style="height:50px;">
      <el-button @click="getInfo()">getInfo按钮</el-button>
      <el-button type="primary" @click="getBlock()">getBlock按钮</el-button>
      <el-button type="success" @click="loginAccount()">loginAccount按钮</el-button>
      <el-button type="info" @click="tran()">tran按钮</el-button>
      <el-button type="warning" disabled>警告按钮</el-button>
      <el-button type="danger" disabled>危险按钮</el-button>
    </el-row>

    <el-row style="height:50px;">
      名称：
      <el-input v-model="info.name" placeholder="请输入姓名" style="width: 50%;"></el-input>
    </el-row>
    <el-row style="height:50px;">
      MD5Code：
      <el-input v-model="info.code" placeholder="请输入年龄" style="width: 50%;"></el-input>
    </el-row>
    <el-row style="height:50px;">
      EOS环境网络：
      <el-select v-model="info.net" placeholder="请选择">
        <el-option v-for="item in options" :key="item.index" :label="item.name" :value="item.url"></el-option>
      </el-select>
      {{info.net}}
    </el-row>

  </div>
</template>
<script>
//eosjs
import { Api, JsonRpc, RpcError } from 'eosjs'
import { JsSignatureProvider } from 'eosjs/dist/eosjs-jssig'
import EosApi from 'eosjs-api'
import { TextEncoder, TextDecoder } from 'text-encoding'
import fetc from 'node-fetch'
export default {
  name: 'EosTest',
  data () {
    return {
      msg: 'test eosjs',
      options: [
        {index:1,name:'主网',url:'mainnet'},
        {index:2,name:'Jungle',url:'jungle'},
        {index:3,name:'麒麟',url:'kylin'}
      ],
      info: {
        name: 'name',
        code: 'code',
        net: null
      }


 

    }
  },
  created(){
    this.info.net = this.options[1].url ;
  },
  methods: {

    tran(){
      const config = {
        keyProvider: ['5JbUxancvip9WtNeANbuonoP7Cbo3zVTfEAMhzTyyvyRdrkfJyx'], // 配置私钥字符串
        httpEndpoint: 'https://api-kylin.eoslaomao.com', //DEV开发链url与端口
        chainId: "5fff1dae8dc8e2fc4d5b23b2c7665c97f9e9d8edf2b6485a86ba311c25639191", // 通过cleos get info可以获取chainId
        mockTransactions: () => null, // 如果要广播，需要设为null
        transactionHeaders: (expireInSeconds, callback) => {
          callback(null/*error*/, headers) //手动设置交易记录头，该方法中的callback回调函数每次交易都会被调用
        },
        expireInSeconds: 60,
        broadcast: true,
        debug: false,
        sign: true,
        authorization: null // 该参数用于在多签名情况下，识别签名帐号与权限,格式如：account@permission
      };
      const api = Eos(config);
      (async () => {
        const result = await api.transact({
          actions: [{
            account: 'eosio.token',
            name: 'transfer',
            authorization: [{
              actor: 'creatortest1',
              permission: 'active',
            }],
            data: {
              from: 'creatortest1',
              to: 'lllluuuuoooo',
              quantity: '0.0001 EOS',
              memo: 'for eosjs test 中国制造',
            },
          }]
        }, {
          blocksBehind: 3,
          expireSeconds: 30,
        });
        console.dir(result);
      })();
    },
    loginAccount(){
      const defaultPrivateKey = "5JbUxancvip9WtNeANbuonoP7Cbo3zVTfEAMhzTyyvyRdrkfJyx"; // bob
      const signatureProvider = new JsSignatureProvider([defaultPrivateKey]);
      const rpc = new JsonRpc('https://api-kylin.eoslaomao.com', { fetch });
      const api = new Api({ rpc, signatureProvider, textDecoder: new TextDecoder("utf-8"), textEncoder: new TextEncoder("utf-8") });
      (async () => {
        const result = await api.transact({
          actions: [{
            account: 'eosio.token',
            name: 'transfer',
            authorization: [{
              actor: 'creatortest1',
              permission: 'active',
            }],
            data: {
              from: 'creatortest1',
              to: 'lllluuuuoooo',
              quantity: '0.0001 EOS',
              memo: 'for eosjs test 中国制造',
            },
          }]
        }, {
          blocksBehind: 3,
          expireSeconds: 30,
        });
        console.dir(result);
      })();
    },
    getInfo() {
      var options = {
        httpEndpoint: 'https://api.kylin.alohaeos.com',
        verbose: false, // API logging
        logger: { // Default logging functions
          //log: config.verbose ? console.log : '',
          error: console.error
        },
        fetchConfiguration: {}
      }
      
      var eos = EosApi(options)

      eos.getInfo({}).then(result => { 
        console.log(result) 
      })
    },
    getBlock(){

      var options = {
        httpEndpoint: 'https://api.kylin.alohaeos.com',
        verbose: false, // API logging
        logger: { // Default logging functions
          //log: config.verbose ? console.log : '',
          error: console.error
        },
        fetchConfiguration: {}
      }
      
      var eos = EosApi(options)

      eos.getBlock(200012).then(result => { 
        console.log('getBlock 200000 ',result)
      })

    }

  }
}

</script>
<style>

</style>


