<template>
  <div>
  
    <el-upload
      class="upload-demo"
      action=""
      :auto-upload=false
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :before-remove="beforeRemove"
      multiple
      :limit="3"
      :on-exceed="handleExceed"
      :file-list="fileList">
      <el-button size="small" type="primary">点击上传</el-button>
      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
    </el-upload>

    <el-row>
      {{mdfile.msg}}
      <el-button type="success">{{msg}}</el-button>
      姓名：{{info.name}}
      <el-input v-model="info.name" placeholder="请输入姓名"></el-input>
      年龄：{{info.age}}
      <el-input v-model="info.age" placeholder="请输入年龄"></el-input>
      性别：{{info.sex}}
      <el-select v-model="info.sex" placeholder="请选择">
        <el-option v-for="item in options" :key="item" :value="item"></el-option>
      </el-select>
    </el-row>

  </div>
</template>
<script>
import SparkMD5 from 'spark-md5'
export default {
  name: 'Test',
  data () {
    return {
      msg: 'test ele-ui',
      info: {
        name: '',
        age: null,
        sex: ''
      },
      options: [
        '女','男','保密'
      ],

      fileList: [],

      mdfile: {
        file: null,
        chunkSize: 2097152, // read in chunks of 2MB
        blobSlice: null,
        currentChunk: 0,
        chunks: 0,
        spark: null,
        end: null,
        msg: ''
      }

    }
  },
  methods: {

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);

      this.mdfile.blobSlice= File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
      this.mdfile.file= file.raw; //获取原生file对象
      this.mdfile.chunks = Math.ceil(file.size / this.mdfile.chunkSize);
      this.mdfile.currentChunk = 0;
      this.mdfile.spark = new SparkMD5.ArrayBuffer();

      this.mdfile.msg="file name: "+file.name+" ("+file.size.toString().replace(/\B(?=(?:\d{3})+(?!\d))/g, ',')+" bytes)\n";
      this.loadNext();

    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    },

    frOnload(e){
      this.mdfile.msg="\nread chunk number "+parseInt(this.mdfile.currentChunk+1)+" of "+this.mdfile.chunks;
      this.mdfile.spark.append(e.target.result); // append array buffer
      this.mdfile.currentChunk++;
      if (this.mdfile.currentChunk < this.mdfile.chunks)
        this.loadNext();
      else
          this.mdfile.msg="FILE: "+this.mdfile.file.name+"  HASH: "+this.mdfile.spark.end();
    },
    frOnerror() {
      console.log("\noops, something went wrong.");
    },
    loadNext() {
      var fileReader = new FileReader();
      fileReader.onload = this.frOnload;
      fileReader.onerror = this.frOnerror;
      var start = this.mdfile.currentChunk * this.mdfile.chunkSize;
      this.mdfile.end = ((start + this.mdfile.chunkSize) >= this.mdfile.file.size) ? this.mdfile.file.size : (start + this.mdfile.chunkSize);
      console.log(window.FileReader);
      console.log(this.mdfile.blobSlice);
      fileReader.readAsArrayBuffer(this.mdfile.blobSlice.call(this.mdfile.file, start, this.mdfile.end));
    }

  }
}

</script>
<style>

</style>


