import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Test from '@/components/test/Test'
import EosTest from '@/components/test/EosTest'
import Creator from '@/components/creator/Creator'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Creator',
      component: Creator
    },{
      path: '/hello',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    {
      path: '/eostest',
      name: 'EosTest',
      component: EosTest
    }
  ]
})



