<template>
  <div>
    <h1>读取文件的MD5值作为文件的凭证，上传到区块链记录初创权归属</h1>
    <el-row style="height:80px;width: 90%;" >
      <el-upload
        class="upload-demo"
        action=""
        :auto-upload=false
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :before-remove="beforeRemove"
        multiple
        :limit="1"
        :on-exceed="handleExceed"
        :file-list="fileList">
        <el-button size="small" type="primary" @click="messageClick()">选择要读取MD5的文件</el-button>
      </el-upload>
    </el-row>
    <el-row style="height:50px;width: 90%;">
      {{mdfile.msg}}
    </el-row>
    <el-row class="item-stype">
      上链内容：
      <el-input v-model="eosmsg.memo" placeholder="限输入256个字符" style="width: 50%;"></el-input>
    </el-row>
    <el-row class="item-stype">
      EOS环境网络：
      <el-select v-model="net.url" placeholder="请选择">
        <el-option v-for="item in options" :key="item.index" :label="item.name" :value="item.url"></el-option>
      </el-select>
      {{net.url}}
    </el-row>
    <el-row class="item-stype">
      账号account：
      <el-input v-model="account.name" placeholder="请输入你的账号" style="width: 50%;"></el-input>
    </el-row>
    <el-row class="item-stype">
      私钥active key：
      <el-input v-model="account.activeKey" placeholder="请输入账号的私钥" style="width: 50%;"></el-input>
    </el-row>

    <el-row style="height:50px;">
      <el-button type="success" @click="sendToBlock()">发送消息到区块链</el-button>
    </el-row>
    <el-row style="height:50px;">
      {{eosmsg.tranHash}}
    </el-row>
  </div>
</template>
<script>
import SparkMD5 from 'spark-md5'
//eosjs
import { Api, JsonRpc, RpcError } from 'eosjs'
import { JsSignatureProvider } from 'eosjs/dist/eosjs-jssig'
import EosApi from 'eosjs-api'
import { TextEncoder, TextDecoder } from 'text-encoding'
import fetc from 'node-fetch'
export default {
  name: 'Creator',
  data () {
    return {
      eosmsg: {
        memo: null,
        tranHash: null
      },
      net: {
        url: null
      },
      account: {
        name: null,
        activeKey: null
      },
      fileList: [],
      mdfile: {
        file: null,
        chunkSize: 2097152, // read in chunks of 2MB
        blobSlice: null,
        currentChunk: 0,
        chunks: 0,
        spark: null,
        end: null,
        msg: ''
      },

      options: [
        {index:1,name:'主网',url:'https://proxy.eosnode.tools'},
        {index:2,name:'Jungle',url:'https://api.jungle.alohaeos.com'},
        {index:3,name:'麒麟',url:'https://api-kylin.eoslaomao.com'}
      ]

    }
  },
  created(){
    this.net.url = this.options[2].url;
    this.account.name = 'creatortest1';
    this.account.activeKey = '5JbUxancvip9WtNeANbuonoP7Cbo3zVTfEAMhzTyyvyRdrkfJyx';
  },
  methods: {

    handleRemove(file, fileList) {
      console.log('handleRemove()');
    },
    handlePreview(file) {
      console.log('handlePreview()');

      this.mdfile.blobSlice= File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
      this.mdfile.file= file.raw; //获取原生file对象
      this.mdfile.chunks = Math.ceil(file.size / this.mdfile.chunkSize);
      this.mdfile.currentChunk = 0;
      this.mdfile.spark = new SparkMD5.ArrayBuffer();

      this.mdfile.msg="FILE: "+file.name+" ("+file.size.toString().replace(/\B(?=(?:\d{3})+(?!\d))/g, ',')+" bytes)\n";
      this.loadNext();

    },
    handleExceed(files, fileList) {
      //this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      this.$message.warning(`当前限制选择 1 个文件，请删除当前文件再选择`);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    },
    messageClick(){
      console.log('messageClick()');
      this.mdfile.msg="点击列表文件开始计算MD5值";
    },
    frOnload(e){
      this.mdfile.msg="计算MD5进度："+parseInt(this.mdfile.currentChunk+1)+" / "+this.mdfile.chunks;
      this.mdfile.spark.append(e.target.result); // append array buffer
      this.mdfile.currentChunk++;
      if (this.mdfile.currentChunk < this.mdfile.chunks)
        this.loadNext();
      else{
        var hash = this.mdfile.spark.end();
        this.mdfile.msg="FILE: "+this.mdfile.file.name+"  HASH: "+hash;
        this.eosmsg.memo = '文件MD5HASH:'+hash+' 创作人:';
      }
    },
    frOnerror() {
      console.log("大清亡啦，程序员已删库跑路");
    },
    loadNext() {
      var fileReader = new FileReader();
      fileReader.onload = this.frOnload;
      fileReader.onerror = this.frOnerror;
      var start = this.mdfile.currentChunk * this.mdfile.chunkSize;
      this.mdfile.end = ((start + this.mdfile.chunkSize) >= this.mdfile.file.size) ? this.mdfile.file.size : (start + this.mdfile.chunkSize);

      fileReader.readAsArrayBuffer(this.mdfile.blobSlice.call(this.mdfile.file, start, this.mdfile.end));
    },

    sendToBlock(){
      this.eosmsg.tranHash = null;
      const defaultPrivateKey = this.account.activeKey; // bob
      const signatureProvider = new JsSignatureProvider([defaultPrivateKey]);
      const rpc = new JsonRpc(this.net.url, { fetch });
      const api = new Api({ rpc, signatureProvider, textDecoder: new TextDecoder("utf-8"), textEncoder: new TextEncoder("utf-8") });
      (async () => {
        const result = await api.transact({
          actions: [{
            account: 'eosio.token',
            name: 'transfer',
            authorization: [{
              actor: this.account.name,
              permission: 'active',
            }],
            data: {
              from: this.account.name,
              to: 'lllluuuuoooo',
              quantity: '0.0001 EOS',
              memo: this.eosmsg.memo,
            },
          }]
        }, {
          blocksBehind: 3,
          expireSeconds: 30,
        });
        console.dir(result);
        this.eosmsg.tranHash = '交易HASH，可在区块浏览器中查看详情：'+result.transaction_id;
      })();
    },
    getBlock(){
      var options = {
        httpEndpoint: this.net.url,//'https://api.kylin.alohaeos.com',
        verbose: false, // API logging
        logger: { // Default logging functions
          //log: config.verbose ? console.log : '',
          error: console.error
        },
        fetchConfiguration: {}
      }
      var eos = EosApi(options)
      eos.getBlock(200012).then(result => { 
        console.log('getBlock 200000 ',result)
      })
      eos.getInfo({}).then(result => { 
        console.log(result) 
      })
    }

  }
}

</script>

<style>
.item-stype {
  margin-left: 250px;
  height: 60px;
  width: 1100px;
  text-align: left;
}
</style>


